.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;
  padding: 50px 0;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: $card-margin;
    transition: color $time-transrpormation, transform $time-transrpormation;

    &:not(:nth-child(4n)) {
      margin-right: $card-margin;
    }
  }

  &__card:hover {
    transform: scale(1.2);
    color: $color-card-hover;
  }
}
